import React, { Component } from 'react'
import {
  faAngleRight,
  faArrowRight,
  faChevronRight,
  faEnvelope,
  faLongArrowAltRight,
} from '@fortawesome/free-solid-svg-icons'
import {
  faGithub,
  faLinkedin,
  faTwitter,
} from '@fortawesome/fontawesome-free-brands'

import Banner from '../Banner'
import Contact from '../Contact'
import Footer from '../Footer'
import NavBar from '../NavBar'
import PropTypes from 'prop-types'
import { library } from '@fortawesome/fontawesome-svg-core'
import styled from 'styled-components'

library.add(
  faAngleRight,
  faArrowRight,
  faChevronRight,
  faLongArrowAltRight,
  faEnvelope,
  faLinkedin,
  faTwitter,
  faGithub
)

const StyledPage = styled.div`
  display: flex;
  flex-direction: column;
  background-color: #2a2f4a;
`

class Layout extends Component {
  constructor(props) {
    super(props)
    this.state = {
      loaded: false,
    }
  }

  componentDidMount() {
    window.onload = () => this.setState({ loaded: true })
  }

  render() {
    return (
      <StyledPage>
        <NavBar
          transparent
          loaded={this.state.loaded}
          logoTitle={this.props.logoTitle}
          logoSubtitle={this.props.logoSubtitle}
        />
        <Banner
          index={this.props.index}
          loaded={this.state.loaded}
          title={this.props.title}
          subtitle={this.props.subtitle}
        />
        {this.props.children}
        {this.props.index === true ? <Contact /> : ''}
        <Footer withContacts={this.props.index === false} />
      </StyledPage>
    )
  }
}

Layout.propTypes = {
  index: PropTypes.bool,
  logoTitle: PropTypes.string,
  logoSubtitle: PropTypes.string,
  title: PropTypes.string,
  subtitle: PropTypes.arrayOf(PropTypes.string),
}

Layout.defaultProps = {
  index: false,
  logoTitle: 'Forty',
  logoSubtitle: 'By HTML5 Up',
  title: 'Hi, my name is Forty',
  subtitle: [
    'A responsive site template designed by HTML5 UP',
    'and released under the creative commons.',
  ],
}

export default Layout
